@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
:root{--black: #071820;--gray: #316851;--light: #86c06c;--white: #dff8d0}body{margin:0;font-family:'Orbitron', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}*{padding:0;margin:0}html,body{height:100%;overflow:hidden;background-color:#071820;background-color:var(--black)}.visualiser-element,.boy-element{position:fixed;top:0;left:0}.startButton{font-size:30px;z-index:1000;cursor:pointer;padding:0;margin:0;text-shadow:5px 5px 5px #316851;position:fixed;margin:0 auto;color:#86c06c;top:calc(50% + 100px);left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%)}.startButton:hover{text-decoration:underline}

.App {
  text-align: center;
